// sliders
function mainSlider() {
    var swiper = new Swiper('.main .swiper-container', {
        slidesPerView: 1,
        spaceBetween: 10,
        loop: true,
        navigation: {
            nextEl: '.main .swiper-button-next',
            prevEl: '.main .swiper-button-prev',
        },
        pagination: {
            el: '.main .swiper-pagination',
            type: 'bullets',
            clickable: true,
        },
    })
}

function brandsSlider() {
    var swiper = new Swiper('.brands .swiper-container', {
        slidesPerView: 'auto',
        spaceBetween: 0,
        navigation: {
            nextEl: '.brands .swiper-button-next',
            prevEl: '.brands .swiper-button-prev',
        },
        scrollbar: {
            el: '.brands .swiper-scrollbar',
            draggable: true,
        },
        breakpoints: {
            320: {
                slidesPerView: 1.7,
            },
            576: {
                slidesPerView: 'auto',
            }
        }
    })
}

function servicesSlider() {
    var swiper = new Swiper('.related-service .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        scrollbar: {
            el: '.related-service .swiper-scrollbar',
            draggable: true,
        },
    })
}

function historySlider() {
    var swiper = new Swiper('.history .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        scrollbar: {
            el: '.history .swiper-scrollbar',
            draggable: true,
        },
    })
}

function tariffsSlider() {
    var swiper = new Swiper('.tariffs .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        scrollbar: {
            el: '.tariffs .swiper-scrollbar',
            draggable: true,
        },
    })
}



function Sliders() {
    let SlidersArray = []
    $(".mob-slider").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        SlidersArray[index] = new Swiper(`.mob-slider[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: "auto",
            spaceBetween: 0,
            observer: true,
            observeParents: true,
            observeSlideChildren: true,
            scrollbar: {
                el: `.mob-slider[data-slider-id="${index}"] .swiper-scrollbar`,
                draggable: true,
            },
        })
    })
}


mainSlider()
brandsSlider()
servicesSlider()
historySlider()
// collectionSlider()
tariffsSlider()
Sliders()
// end sliders
